















import { Component, Prop, Vue, Emit } from "vue-property-decorator";

@Component
export default class Nav extends Vue {
  @Prop() private navList!: [];
  private cur = -1;
  get navShow() {
    return this.navList.length > 0;
  }
  @Emit("clickNav")
  public clickNav(item: any, index: number) {
    this.cur = index;
  }
}
